import Vue from 'vue'

/**
 *  
 * <el-button v-debounce="[reset,`click`,300]"></el-button>
 * <el-button v-debounce="[reset]"></el-button>
 */
Vue.directive('debounce', {
	inserted: function (el, binding) {
		let [fn, event = "click", time = 300] = binding.value
		let timer
		el.addEventListener(event, () => {
			timer && clearTimeout(timer)
			timer = setTimeout(() => fn(), time)
		})
	}
})

/**
 *  
 * <el-button v-throttle="[reset,`click`,300]"></el-button>
 * <el-button v-throttle="[()=>reset(param),`click`,300]"></el-button>
 */
Vue.directive('throttle', {
	inserted: function (el, binding) {
		let [fn, event = "click", time = 300] = binding.value
		let now, preTime
		el.addEventListener(event, () => {
			now = new Date()
			if (!preTime || now - preTime > time) {
				preTime = now
				fn()
			}
		})
	}
})
